footer {
  padding-bottom: 2%;
}

.f-bnr {
  padding: 3.125% 0;
  background: url(/common/img/bg_footer_border_01.gif) repeat 0 0;
  li {
    &:nth-child(n+2) {
      margin-top: 20px;
    }
  }
}

@media #{$min961} {
  .f-bnr {
    ul {
      max-width: 660px;
      margin: 0 auto;
      @each $prefix in $prefixList {
        #{$prefix}display: flex;
        #{$prefix}justify-content: space-between;
        #{$prefix}flex-wrap: wrap;
      }
    }
  }
}

@media #{$max960} {
  .f-bnr {
    padding: 6.9% 0;
    ul {
      font-size: 0;
      li {
        &:nth-child(n+3) {
          width: 48.3%;
          display: inline-block;
        }
        &:nth-child(4) {
          margin-left: 2.9%;
        }
      }
    }
  }
}

.f__nav {
  padding: 0.7% 0;
  background: url(/common/img/bg_footer_border_02.gif) repeat 0 0;
  ul {
    @each $prefix in $prefixList {
      #{$prefix}display: flex;
      #{$prefix}justify-content: center;
    }
    li {
      font-size: 1.2rem;
      a {
        display: block;
        padding: 0 20px;
        color: #fff;
        text-align: center;
        &:hover {
          text-decoration: underline;
        }
      }
      @media #{$min480} {
        &:not(:last-child) a {
          border-right: 1px #fff solid;
        }
      }
    }
  }
}

.f__nav {
  @media #{$max960} {
    padding: 1.87% 0;
    ul {
      @each $prefix in $prefixList {
        #{$prefix}flex-wrap: nowrap;
      }
      li {
        font-size: 1.0rem;
        a {
          padding: 0 10px;
        }
      }
    }
  }
  @media #{$max480} {
    padding: 3% 0;
    .inner {
      width: 100%;
      li {
        line-height: 1.2;
        @each $prefix in $prefixList {
          #{$prefix}display: flex;
          #{$prefix}align-items: center;
        }
        &:not(:last-child) {
          border-right: 1px #fff solid;
        }
      }
    }
  }
}

.f-info {
  color: $main_blue01;
  padding: 1.4% 0 1.6%;
  &__logo {
    width: 17.6%;
    margin-right: 1.7%;
  }
  .inner {
    .info-txt-line02 {
      .info-tel {
        a {
          color: $main_blue01;
        }
      }
    }
    .info-txt-line03 {
      color: $normal_font;
    }
  }
}

@media #{$min961} {
  .f-info {
    .inner {
      @each $prefix in $prefixList {
        #{$prefix}display: flex;
        #{$prefix}justify-content: center;
        #{$prefix}align-items: center;
      }
      .f-info-txt {
        line-height: 1.3;
      }
      .info-txt-line01 {
        font-size: 1.6rem;
      }
      .info-txt-line02 {
        .info-tel {
          font-size: 2rem;
          a {
            &:hover {
              cursor: default;
            }
          }
          strong {
            font-size: 3.6rem;
          }
          .info-time {
            font-size: 1.2rem;
          }
        }
      }
      .info-txt-line03 {
        font-size: 1rem;
      }
    }
  }
}

@media #{$min480_max768} {
  .f-info {
    .inner {
      .info-txt-line01 {
        font-size: 1.4rem;
      }
    }
  }
}

.f-copy {
  font-size: 1.2rem;
}

@media #{$max960} {
  .f-info {
    padding: 5.2% 0 3.125%;
    line-height: 1.4;
    &__logo {
      width: 20.9%;
      margin-right: 3%;
    }
    .inner {
      .f-info-txt {
        .f-info-sp01 {
          @each $prefix in $prefixList {
            #{$prefix}display: flex;
            #{$prefix}justify-content: center;
            #{$prefix}align-items: center;
          }
          .info-txt-line01 {
            width: calc(100% - 23.9%);
            font-size: 1.2rem;
          }
        }
        .f-info-sp02 {
          padding: 3%;
          margin-top: 4.65%;
          border: 5px $main_blue01 solid;
          .info-txt-line02 {
            .txt-tel {
              padding-left: 3%;
            }
            .txt-tel,
            .ico-tel {
              display: table-cell !important;
              vertical-align: middle;
            }
          }
          .ico-tel {
            width: 28%;
          }
        }
      }
      .info-txt-line02 {
        display: table;
        width: 100%;
        .info-tel {
          font-size: 1.5rem;
          strong {
            font-size: 2rem;
          }
        }
        .info-time {
          font-size: 1rem;
        }
      }
      .info-txt-line03 {
        margin-top: 3%;
        font-size: 1.2rem;
      }
    }
  }
}

.btn-top {
  position: fixed;
  bottom: 0;
  right: 0;
  z-index: 100;
  width: 80px;
  opacity: 0.8;
  &:hover {
    opacity: 1;
  }
  @media #{$max480} {
    width: 50px;
  }
}
