//animation
.fadein-up {
  opacity: 0;
  -webkit-animation: fadeInUp 0.5s ease 1s 1 forwards;
  animation: fadeInUp 0.5s ease 1s 1 forwards;
}

.fadein {
  opacity: 0;
  transform: translate(0, 10px);
  transition: all 600ms;
}


/* 画面内に入った状態 */

.fadein.scrollin {
  opacity: 1;
  transform: translate(0, 0);
}

