@mixin font-heavy {
  font-family: "M+ C Type-1 (basic latin) Heavy", "M+ Type-1 (general-j) Heavy";
  font-style: italic;
  font-weight: bold;
  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    font-family: "Hiragino Kaku Gothic ProN",Meiryo !important;
  }
  /* IE11 */
  @media all and (-ms-high-contrast:none) {
    *::-ms-backdrop {
    font-family: "Hiragino Kaku Gothic ProN",Meiryo !important;
    }
  }

}



@mixin font-black {
  font-family: "M+ C Type-1 (basic latin) black", "M+ Type-1 (general-j) black";
  font-style: italic;
    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    font-family: "Hiragino Kaku Gothic ProN",Meiryo !important;
    font-weight: bold;
  }
  /* IE11 */
  @media all and (-ms-high-contrast:none) {
    *::-ms-backdrop {
    font-family: "Hiragino Kaku Gothic ProN",Meiryo !important;
      font-weight: bold;
    }
  }

}

@mixin font-meiryo {
  font-family: "Hiragino Kaku Gothic ProN", "Meiryo";
  font-style: normal;
}

