@media #{$min961} {
  .l-header {
    max-width: 1020px;
    margin: 0 auto;
  }
}

.header {
  border-bottom: 1px $main_blue01 solid;
  position: relative;
  @include font-heavy;
  a {
    text-decoration: none;
  }
}

@media #{$min961} {
  .l-header {
    //    padding: 1.1% 0;
    @each $prefix in $prefixList {
      #{$prefix}display: flex;
      #{$prefix}justify-content: space-between;
      #{$prefix}align-items: center;
    }
    .header__inner {
      margin: 0 0 0 1.9%;
      width: 15.5%;
    }
  }
  .h__nav {
    width: calc(100% - 17.3%);
    &-list {
      @each $prefix in $prefixList {
        #{$prefix}display: flex;
        #{$prefix}justify-content: flex-end;
        #{$prefix}align-items: center;
      }
      .nav-list__item {
        font-size: 1.8rem;
        font-weight: bold;
        .nav-main__ttl {
          color: $main_blue01;
          display: block;
          padding: 30px 25px;
        }
      }
    }
  }
}

@media #{$max960} {
  .l-header {
    .header__inner {
      position: relative;
      z-index: 200;
      padding: 5.2% 4.16%;
      @each $prefix in $prefixList {
        #{$prefix}display: flex;
        #{$prefix}justify-content: space-between;
        #{$prefix}align-items: center;
      }
    }
    .h__logo {
      width: 44.7%;
    }
    .h__nav-btn {
      width: 10%;
    }
    .nav-list__item {
      &:last-child a {
        border-bottom: 1px $main_blue01 solid;
      }
    }
    //    @media #{$min769_max960} {
    //      .header__inner {
    //        padding: 2% 4.16%;
    //      }
    //    }
  }
}

//js
@media #{$min961} {
  .nav-list__item {
    &.current,
    &:hover {
      .nav-main__ttl {
        position: relative;
        &:after {
          content: "";
          position: absolute;
          border-style: solid;
          border-width: 0 15px 17px 15px;
          border-color: transparent transparent $main_blue01 transparent;
          bottom: 0;
          left: 50%;
          display: block;
          transform: translateX(-50%);
        }
      }
    }
    &:hover {
      .mega-menu {
        display: block;
      }
    }
    //    .mega-menu {
    //      display: block;
    //    }
  }
}

@media #{$min961} {
  .mega-menu {
    position: absolute;
    width: 100%;
    left: 0;
    top: 100%;
    padding: 1.56% 0;
    background: $main_blue01;
    width: 100%;
    z-index: 100;
    display: none;
    &__inner {
      max-width: 1020px;
      margin: 0 auto;
      @each $prefix in $prefixList {
        #{$prefix}display: flex;
        #{$prefix}justify-content: space-between;
      }
      .mega-menu__items {
        background: #fff;
        width: 31.4%;
        padding-bottom: 35px;
        @each $prefix in $prefixList {
          #{$prefix}display: flex;
          #{$prefix}flex-wrap: wrap;
        }
        .mega-menu__head {
          .list01__thum {
            display: block;
          }
          .ttl__mega {
            text-align: center;
            font-size: 2rem;
            color: $main_blue01;
          }
        }
        .mega-menu-list {
          padding: 6.25% 0 0 9.37%;
          font-size: 1.4rem;
          display: block;
          width: 100%;
          @include font-meiryo;
          li {
            +li {
              margin-top: 10px;
            }
            a {
              &:hover {
                text-decoration: underline;
              }
            }
          }
          &-item {
            a {
              color: $main_blue01;
            }
          }
        }
      }
    }
  }
}

.nav-arrow {
  .header & {
    @media #{$min961} {
      display: inline-block;
      padding-left: 10px;
      vertical-align: middle;
      img {
        vertical-align: top;
      }
    }
  }
  .sitemap & {
    display: inline-block;
    padding-left: 10px;
    vertical-align: middle;
    img {
      vertical-align: top;
    }
  }
}

@media #{$max960} {
  .header {
    position: relative;
  }
  .h__nav {
    position: absolute;
    display: none;
    top: 100%;
    left: 0;
    z-index: 100;
    background: #fff;
    width: 100%;
    .nav-list__item {
      a {
        position: relative;
        display: block;
        border-top: 1px $main_blue01 solid;
        color: $main_blue02;
        font-size: 1.7rem;
        &:after {
          content: "";
          position: absolute;
          border-style: solid;
          border-width: 7.5px 0 7.5px 12px;
          border-color: transparent transparent transparent $main_blue02;
          right: 5.2%;
          top: 39%;
        }
      }
      .sp__nav--type01 {
        padding: 3.7% 5.2% 3.7% 5.2%;
      }
      .sp__nav--type02 {
        padding: 6.4% 5.2% 6.4% 5.2%;
      }
    }
    &:after {
      content: "";
      position: fixed;
      background: rgba(255, 255, 255, 0.5);
      z-index: 99;
      width: 100%;
      height: 100%;
      display: block;
      top: 0;
      left: 0;
    }
    &-list {
      position: relative;
      z-index: 100;
    }
  }
  .mega-menu {
    &__inner {
      .mega-menu__items {
        .mega-menu__head {
          position: relative;
          .list01__thum,
          .ttl__mega {
            display: inline-block;
            vertical-align: middle;
          }
          .list01__thum {
            width: 15.3%;
            margin-right: 2%;
          }
        }
      }
    }
  }
}
