.bg {
  &-gray {
    background: #f9f9f9;
  }
  &-gray--gradetion {
    @media #{$max960} {
      background: rgb(255, 255, 255);
      /* Old browsers */
      background: -moz-linear-gradient(top, rgba(255, 255, 255, .3) 0%, rgba(204, 204, 204, .3) 100%);
      /* FF3.6-15 */
      background: -webkit-linear-gradient(top, rgba(255, 255, 255, .3) 0%, rgba(204, 204, 204, .3) 100%);
      /* Chrome10-25,Safari5.1-6 */
      background: linear-gradient(to bottom, rgba(255, 255, 255, .3) 0%, rgba(204, 204, 204, .3) 100%);
      /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    }
  }
}

//ttl_primary
@mixin ttl_effect($padding:0 0, $fz:4.8rem) {
  padding: $padding;
  font-size: $fz;
  font-weight: bold;
  text-align: center;
  color: #fff;
  background: -webkit-gradient(radial, 49% 50%, 0, 49% 50%, 122, color-stop(0.88, #2c2c51), color-stop(0.00, #213271));
  background: -webkit-radial-gradient(49% 50%, circle, #213271 0%, #2c2c51 88%);
  background: -moz-radial-gradient(49% 50%, circle, #213271 0%, #2c2c51 88%);
  background: -o-radial-gradient(49% 50%, circle, #213271 0%, #2c2c51 88%);
  background: -ms-radial-gradient(49% 50%, circle, #213271 0%, #2c2c51 88%);
  background: radial-gradient(49% 50%, circle, #213271 0%, #2c2c51 88%);
  @include font-black;
}

.ttl-primary {
  @include ttl_effect(2% 0, $fz: 3rem);
  @media #{$max960} {
    font-size: 3rem;
  }
  @media #{$max480} {
    font-size: 1.5rem;
  }
}

.ttl-primary--sm {
  @include ttl_effect(1.3% 0, 3.0rem);
  @include font-heavy;
  @media #{$max768} {
    font-size: 3rem;
  }
  @media #{$max480} {
    padding: 4% 0 3%;
    font-size: 1.5rem;
  }
}

//btn-detai
//pc ver
.btn-detail {
  position: relative;
  display: block;
  padding: 3.3% 0;
  text-align: center;
  -webkit-transform: skewX(-16deg);
  transform: skewX(-16deg);
  font-weight: bold;
  color: #fff;
  @include font-meiryo;
  background: $main_blue01;
  &__txt {
    display: block;
    -webkit-transform: skewX(16deg);
    transform: skewX(16deg);
    background: url(/common/img/ico_btn_arrow_o1.gif) no-repeat right 10.6% center / 13px auto;
    transition: .5s;
    transition-property: all;
    @media #{$max960} {
      background-size: 7px auto;
    }
  }
  @media #{$min480_max768} {}
  @media #{$max960} {
    float: left;
  }
  &--big {
    @extend .btn-detail;
    float: none;
    max-width: 560px;
    margin: 50px auto 0;
    padding: 29px 0 28px;
    font-size: 2.4rem;
    @media #{$max960} {
      max-width: 82%;
      margin-top: 30px;
      padding: 20px 0 18px;
      font-size: 2.0rem;
    }
  }
  &--mid {
    @extend .btn-detail;
    font-size: 2rem;
    @media #{$min961} {
    max-width: 375px;
    margin: 0 auto;
    }
    @media #{$min480_max768} {
      .btn-detail__txt {
        background-size: 4px auto;
      }
    }
    @media #{$max960} {
      margin-top: 7.5%;
      width: 50.8%;
      font-size: 1.5rem;
      padding: 3.3% 0;
    }
  }
  &--mid--02 {
    @extend .btn-detail--mid;
    font-size: 2.4rem;
    @media #{$min961} {
    max-width: 300px;
    margin: 0 auto;
    }
    @media #{$max480} {
      font-size: 1.4rem;
    }
  }
  &--sm {
    @extend .btn-detail;
    max-width: 215px;
    font-size: 1.4rem;
    .btn-detail__txt {
      background: url(/common/img/ico_btn_arrow_o1.gif) no-repeat right 10.6% center / 5px auto;
    }
    @media #{$max960} {
      float: none;
    }
  }
  &--topbien01 {
    @extend .btn-detail;
    font-size: 2.4rem;
    @media #{$min961} {
      margin-top: 12.5%;
      width: 50.8%;
      padding: 29px 0 28px;
    }
    @media #{$max960} {
      margin-top: 7.5%;
      width: 50.8%;
      font-size: 1.5rem;
      padding: 3.3% 0;
    }
    @media #{$min480_max768} {
      .btn-detail__txt {
        background-size: 4px auto;
      }
    }
    @media #{$max480} {
      font-size: 1.4rem;
    }
  }
}

.animation__arrow {
  &:hover {
    .animation__arrow--move {
      @media #{$min961} {
        background: url(/common/img/ico_btn_arrow_o1.gif) no-repeat right 7.6% center / 13px auto;
      }
    }
  }
}

.animation__arrow {
  &:hover {
    .btn-detail--sm {
      .animation__arrow--move {
        @media #{$min961} {
          background: url(/common/img/ico_btn_arrow_o1.gif) no-repeat right 7.6% center / 5px auto;
        }
      }
    }
  }
}

.top {
  .sekitan {
    .btn-detail {
      @media #{$min961} {
        width: 58.75%;
      }
      @media #{$max768} {
        font-size: 2.4rem;
      }
      @media #{$max480} {
        font-size: 1.4rem;
      }
    }
  }
}

//block-sub-navigation
.block-sub-navigation {
  padding: 0.67% 0;
  background: $main_blue01;
  @media #{$max960} {
    padding: 1.3% 0;
  }
}

//intro_cmn
.intro-cmn {
  padding: 5.2% 0;
  font-size: 2.4rem;
  text-align: center;
  line-height: 1.5;
  color: $main_blue01;
  @media #{$max768} {
    font-size: 1.6rem;
  }
  @media #{$max480} {
    padding: 10.4% 0 9.16%;
  }
}

.intro-cmn-02 {
  @extend .intro-cmn;
  padding: 5.2% 0 3.1%;
  @media #{$max480} {
    padding: 10.4% 0 9.16%;
  }
}

.top {
  .intro-cmn {
    @media #{$min961} {
      padding: 5% 0;
    }
    @media #{$max960} {
      width: 86.9%;
      margin: 0 auto;
      padding: 7.2% 0;
    }
    @media #{$max480} {
      padding: 10.4% 0 9.16%;
    }
  }
}

//section-cmn
.section {
  &-cmn {
    padding: 40px 0 60px;
    @media #{$max960} {
      padding: 7.8% 0 7.8%;
    }
  }
}
