.gx {
  background: url(/campaign/2018/img/bg_main.jpg) center center / cover no-repeat;
  &__inner {
    width: 95%;
    max-width: 645px;
    margin: 0 auto;
  }
  &__ttl {
    padding: 77px 0 43px;
    text-align: center;
    @media #{$max767} {
      padding: 40px 0 30px;
      img {
        width: 70%;
      }
    }
  }
  &-main {
    background: $main_blue02;
    &__inner {
      position: relative;
      max-width: 721px;
      margin: 0 auto;
      .slick-arrow {
        display: none !important;
      }
    }
    &__arrow {
      position: absolute;
      top: 50%;
      margin-top: -16px;
      width: 32px;
      height: 34px;
      background-size: cover;
      background-color: transparent;
      border: 0;
      cursor: pointer;
      @media #{$max800} {
        top: auto;
        bottom: 10px;
        width: 20px;
        height: 30px;
        margin-top: 0;
        background-size: 100%;
        background-repeat: no-repeat;
        background-position: center center;
      }
      &--left {
        left: -52px;
        background-image: url(/campaign/2018/img/ico_arrow_left.png);
        @media #{$max800} {
          left: 10px;
        }
      }
      &--right {
        right: -52px;
        background-image: url(/campaign/2018/img/ico_arrow_right.png);
        @media #{$max800} {
          right: 10px;
        }
      }
      &:focus {
        outline: 0;
      }
    }
  }
  &-product {
    padding: 72px 0 75px;
    text-align: center;
    @media #{$max767} {
      padding: 50px 0;
    }
    &__ttl {
      @media #{$max767} {
        width: 75%;
        margin: 0 auto;
      }
    }
    &__detail {
      display: -webkit-flex;
      display: -moz-flex;
      display: -ms-flex;
      display: -o-flex;
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      margin-top: 65px;
      @media #{$max767} {
        display: block;
        width: 75%;
        margin: 45px auto 0;
      }
      img {
        transition: .3s;
      }
      &:hover {
        img {
          opacity: .7;
        }
      }
    }
    &__img {
      flex-basis: 46.66%;
      max-width: 46.66%;
      order: 1;
      @media #{$max767} {
        max-width: 65%;
        margin: 0 auto;
      }
    }
    &__txt {
      flex-basis: 43.56%;
      max-width: 43.56%;
      @media #{$max767} {
        margin-top: 30px;
        max-width: none;
      }
      order: 0;
    }
  }
}