html {
  font-size: 62.5%;
  background: #FFF;
}

body {
  font-size: 1.4rem;
  font-family: "Hiragino Kaku Gothic ProN", "Meiryo";
  color: #333;
  line-height: 1;
}

img,
svg {
  max-width: 100%;
  vertical-align: bottom;
  backface-visibility: hidden;
}

//.pc {
//  display: block;
//  @media #{$max767} {
//    display: none;
//  }
//}
.pc {
  display: block;
  @media #{$max960} {
    display: none !important;
  }
}

//.sp {
//  display: none;
//  @media #{$max767} {
//    display: block;
//  }
//}
.sp {
  display: none !important;
  @media #{$max960} {
    display: block !important;
  }
}

.only-sp {
  display: none;
  @media #{$max480} {
    display: block;
  }
}

.only-tb {
  display: none;
  @media #{$min480_max768} {
    display: block;
  }
}

.min-tb {
  @media #{$max767} {
    display: none !important;
  }
}
.min-sp {
  @media #{$max374} {
    display: none !important;
  }
}


.only-sp-02 {
  display: none;
  @media #{$max767} {
    display: block;
  }
}

.sp-tb {
  display: none;
  @media #{$max768} {
    display: block;
  }
}

.inner {
  max-width: 1060px;
  padding-right: 20px;
  padding-left: 20px;
  margin: 0 auto;
  @media #{$max960} {
    width: 89.58%;
    margin: 0 auto;
    padding-right: 0;
    padding-left: 0;
  }
}

.container__inner {
  margin: 5.2% auto;
  @media #{$max960} {
    .sitemap & {
      margin: 0 auto;
    }
  }
}

.wrapper {
  position: relative;
}

.alpha {
  &-item {
    transition: .3s;
    &:hover {
      opacity: .75;
    }
  }
}

.link-border {
  &-item {
    &:hover {
      text-decoration: underline;
    }
  }
}
